<template>
  <div id="dashProducts">
    <div class="mt-4 text-right">
      <button
        type="button"
        class="newBtn btn btn-primary"
        @click="openModal('new')"
      >
        
      </button>
    </div>
    <div class="table-responsive">
      <table class="table mt-4">
        <thead>
          <tr>
            <th width="120">
              
            </th>
            <th></th>
            <th width="120">
              
            </th>
            <th width="120">
              
            </th>
            <th width="100">
              
            </th>
            <th width="120">
              
            </th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item) in products"
            :key="item.id"
          >
            <td>{{ item.category.split(">")[0] }}</td>
            <td>{{ item.title }}</td>
            <td class="text-right">
              {{ item.origin_price }}
            </td>
            <td class="text-right">
              {{ item.price }}
            </td>
            <td style="vertical-align: middle">
              <label class="toggle-control">
                <input
                  v-model="item.enabled"
                  type="checkbox"
                  @change="enabledProduct(item)"
                >
                <span class="control" />
              </label>
            </td>
            <td>
              <div class="btn-group">
                <button
                  type="button"
                  class="btn btn-outline-primary btn-sm"
                  @click="openModal('edit',item)"
                >
                  
                </button>
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="openModal('delete',item)"
                >
                  
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="text-right">
      <button
        disabled
        type="button"
        class="btn btn-danger mr-5"
        @click="openDelAllProducts"
      >
        
      </button>
    </div>
    <BasePagination
      :pages="pagination"
      @change-page="getProducts"
    />
    <!---------- Modal ---------------------->
    <DashProductModal
      ref="dashProductModal"
      :is-new="isNew"
      @update="getProducts"
      @cancel="clearModal"
    />
    <DashDelProductModal
      ref="dashDelProductModal"
      :temp-product.sync="tempProduct"
      @update="getProducts"
    />
    <DashOtherModal
      id="delAll"
      :title="''"
      :modal-use="'delete'"
      @delete="delAllProducts"
    >
      <template v-slot:delete>
        <div class="p-3 fz-35">
          
        </div>
      </template>
    </DashOtherModal>
  </div>
</template>
<script>
import $ from 'jquery';
import DashOtherModal from '@/components/admin/DashOtherModal.vue';
import DashProductModal from '@/components/DashProductModal.vue';
import DashDelProductModal from '@/components/DashDelProductModal.vue';
import BasePagination from '@/components/BasePagination.vue';

import { instanceAdmin } from '../../api/https';

export default {
  components: {
    DashProductModal,
    DashDelProductModal,
    BasePagination,
    DashOtherModal,
  },
  /**
     * @param products object list
     * @param pagination 
     * @param isNew 
     * @param tempProduct 
     */
  data() {
    return {
      products: [],
      pagination: {},
      isNew: true,
      tempProduct: {
        imageUrl: [],
        options: {
          stock: 0,
          delivery: '',
          specification: '',
        },
      },
      currentPage: '0',
    };
  },
  created() {
    this.currentPage = this.$route.query.page;
    this.getProducts(this.currentPage);
  },
  methods: {
    getProducts(page = 1, paged = 10, orderBy = 'created_at', sort = 'desc') {
      this.$router.push({
        query: { page },
      })
        .catch(() => {});
      const vm = this;
      vm.$store.commit('LOADING', true);
      this.tempProduct = {
        imageUrl: [],
      };
      instanceAdmin.get(`ec/products?page=${page}&paged=${paged}&orderBy=${orderBy}&sort=${sort}`)// 500 err
        .then((res) => {
          vm.products = res.data.data;
          vm.pagination = res.data.meta.pagination;
          vm.$store.commit('LOADING', false);
        });
    },
    openModal(action, item) {
      switch (action) {
        case 'new':
          this.isNew = true;
          this.$refs.dashProductModal.openNewModal();
          break;// %%
        case 'edit':
          this.tempProduct = JSON.parse(JSON.stringify(item));
          this.isNew = false;
          this.$refs.dashProductModal.openEditModal(this.tempProduct.id);
          break;
        case 'delete':
          this.tempProduct = JSON.parse(JSON.stringify(item));
          this.$refs.dashDelProductModal.openDelModal();
          break;
        default:
          break;
      }
    },
    clearModal() {
      // tempProduct modal 
      this.tempProduct = {
        imageUrl: [],
      };
    },
    enabledProduct(item) {
      this.$store.commit('LOADING', true);
      const api = `ec/product/${item.id}`;
      this.$instanceAdmin.patch(api, item)
        .then(() => {
          this.$store.commit('LOADING', false);
        });
    },
    delAllProducts() {
      this.$store.commit('LOADING', true);
      const productIds = this.products.map((prdItem) => prdItem.id);
      this.$http.all(productIds.map((id) => {
        const api = `ec/product/${id}`;
        return this.$instanceAdmin.delete(api);
      }))
        .then(() => {
          this.$store.commit('LOADING', false);
          this.getProducts();
        });
    },
    openDelAllProducts() {
      $('#delAll').modal('show');
    },
  },
};
</script>

<style lang="scss">
  #dashProducts{
    .table{
      min-width: 800px;
    }
  }
</style>
