<template>
  <div
    id="productModal"
    class="modal fade"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div
      class="modal-dialog modal-xl"
      role="document"
    >
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5
            id="exampleModalLabel"
            class="modal-title"
          >
            <span></span>
          </h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
            @click="cancelUpdateProduct"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="form-group">
                <label for="imageUrl">(enter)</label>
                <input
                  id="imageUrl"
                  v-model="tempImgUrl"
                  type="text"
                  class="form-control"
                  placeholder=""
                  @keyup.enter="addImg"
                >
              </div>
              <div class="form-group">
                <label for="fileImg"></label>
                <input
                  id="fileImg"
                  ref="file"
                  type="file"
                  class="form-control"
                  @change="uploadImg"
                >
              </div>
              <hr>
              <div
                class="d-flex w-100 flex-column overflow-x-hidden overflow-y-auto"
                style="height:500px"
              >
                <div
                  v-for="(url,index) in tempProduct.imageUrl"
                  :key="'img'+index"
                  class="w-100 flex-shrink-0 px-2 text-center"
                >
                  <div class="form-group">
                    <label :for="'img'+index"></label>
                    <input
                      :id="'img'+index"
                      v-model="tempProduct.imageUrl[index]"
                      type="text"
                      class="form-control"
                      placeholder=""
                    >
                  </div>
                  <img
                    class="img-fluid"
                    alt=""
                    :src="tempProduct.imageUrl[index]"
                  >
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  id="title"
                  v-model="tempProduct.title"
                  type="text"
                  class="form-control"
                  placeholder=""
                >
              </div>
              <div class="form-row">
                <div class="form-group col-md-4">
                  <label for="category"></label>
                  <input
                    id="category"
                    v-model="tempProduct.category"
                    type="text"
                    class="form-control"
                    placeholder=""
                  >
                </div>
                <div class="form-group col-md-4">
                  <label for="price"></label>
                  <input
                    id="unit"
                    v-model="tempProduct.unit"
                    type="text"
                    class="form-control"
                    placeholder=""
                  >
                </div>
                <div class="form-group col-md-4">
                  <label for="stock"></label>
                  <input
                    id="stock"
                    v-model.number="tempProduct.options.stock"
                    type="number"
                    class="form-control"
                    placeholder=""
                  >
                </div>
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    id="origin_price"
                    v-model="tempProduct.origin_price"
                    type="number"
                    class="form-control"
                    placeholder=""
                  >
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="price"
                    v-model="tempProduct.price"
                    type="number"
                    class="form-control"
                    placeholder=""
                  >
                </div>
              </div>
              <hr>
              <div class="form-group">
                <label for="hashtag"></label>
                <input
                  id="hashtag"
                  v-model="tempProduct.options.hashtag"
                  type="text"
                  class="form-control"
                  placeholder=""
                >
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea
                  id="description"
                  v-model="tempProduct.content"
                  type="text"
                  class="form-control"
                  placeholder=""
                  style="height:60px"
                />
              </div>
              <div class="form-group">
                <label for="description"></label>
                <VueEditor
                  id="description"
                  v-model="tempProduct.description"
                  class=""
                  style=""
                  :placeholder="''"
                  @image-added="uploadEditerImg"
                />
              </div>
              <div class="form-group">
                <label for="specification"></label>
                <textarea
                  id="specification"
                  v-model="tempProduct.options.specification"
                  type="text"
                  class="form-control"
                  placeholder=""
                  style="height:120px"
                />
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input
                    id="is_enabled"
                    v-model="tempProduct.enabled"
                    class="form-check-input"
                    type="checkbox"
                  >
                  <label
                    class="form-check-label"
                    for="is_enabled"
                  ></label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-dismiss="modal"
            @click="cancelUpdateProduct"
          >
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="updateProduct"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
// import { VueEditor, Quill } from 'vue2-editor';
import { VueEditor } from 'vue2-editor';
import { instanceAdmin } from '../api/https';

export default {
  /**
   * @param isNew 
   * @param tempProduct imageUrlmodal
   * @param tempImgUrl 
   * @param filePath 
   * @param status  icon-[]
   */
  components: {
    VueEditor,
  },
  props: {
    isNew: Boolean,
  },
  data() {
    return {
      tempProduct: {
        imageUrl: [],
        options: {
          stock: 0,
          delivery: '',
          specification: '',
        },
      },
      tempImgUrl: '',
      filePath: '',
    };
  },
  watch: {
    'tempProduct.imageUrl': {
      handler(Val) {
        let emptyIndex = 0;
        const ifHasEmpty = Val.some((item, index) => {
          emptyIndex = index;
          return item === '';
        });
        if (ifHasEmpty) {
          this.removeBlankImg(emptyIndex);
        }
      },
      deep: true,
    },
  },
  methods: {
    openNewModal() {
      this.$store.commit('LOADING', true);
      // 
      this.tempProduct = {
        imageUrl: [],
        options: {
          stock: 0,
          delivery: '',
          specification: '',
        },
      };
      this.$store.commit('LOADING', false);
      $('#productModal').modal('show');
    },
    openEditModal(id) { // 
      this.$store.commit('LOADING', true);
      const api = `ec/product/${id}`;
      instanceAdmin.get(api)
        .then((res) => {
          this.tempProduct = res.data.data;// ##
          this.$store.commit('LOADING', false);
          $('#productModal').modal('show');
        });
    },
    addImg() {
      this.tempProduct.imageUrl.push(this.tempImgUrl);
      this.tempImgUrl = '';
    },
    uploadImg() {
      this.$store.commit('LOADING', true);
      const api = 'storage';

      const uploadedFile = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append('file', uploadedFile);

      instanceAdmin.post(api, formData, {
        headers: {
          'content-type': 'multipart/form-data',
        },
      })
        .then((res) => {
          this.$store.commit('LOADING', false);
          this.filePath = res.data.data.path;
          this.tempProduct.imageUrl.push(this.filePath);
        });
    },
    uploadEditerImg(file, Editor, cursorLocation, resetUploader) {
      this.$store.commit('LOADING', true);

      const formData = new FormData();
      formData.append('descImg', file);

      const api = 'storage';
      instanceAdmin.post(api, formData)
        .then((res) => {
          this.$store.commit('LOADING', false);
          const filePath = res.data.data.path;
          Editor.insertEmbed(cursorLocation, 'descImg', filePath);
          resetUploader();
        });
    },
    updateProduct() {
      this.$store.commit('LOADING', true);
      let api = 'ec/product';
      let httpMethod = 'post';
      // 1 
      if (!this.isNew) { // #tempProduct.id
        api = `ec/product/${this.tempProduct.id}`;
        httpMethod = 'patch';
      }
      // 2 api
      instanceAdmin[httpMethod](api, this.tempProduct)
        .then(() => {
          this.$store.commit('LOADING', false);
          // 3 emit
          this.$emit('update');
          // 4 modal
          $('#productModal').modal('hide');

          this.tempProduct = { // 
            imageUrl: [],
            options: {},
          };
        });
    },
    cancelUpdateProduct() {
      this.tempProduct = { // #prop
        imageUrl: [],
        options: {},
      };
      this.$emit('cancel');
    },
    removeBlankImg(emptyIndex) {
      this.tempProduct.imageUrl.splice(emptyIndex, 1);
    },
  },
};

</script>

